import React from "react";

import aboutMe_1 from "../../assets/imgs/main/aboutMe_1.jpeg";
import aboutMe_2 from "../../assets/imgs/main/aboutMe_2.jpeg";
import aboutMe_3 from "../../assets/imgs/main/aboutMe_3.jpeg";

import classes from "./aboutMe.module.scss";

export const AboutMe = () => {
  const textArr = [
    "Nazywam si Angelika, a moja magiczna historia zacza si jeszcze przed moimi narodzinami. Nazywam si Angelika, a moja magiczna historia zacza si jeszcze przed moimi narodzinami. ",
    "Poszam w ich lady i od dziecka uczyam si naszego rodzinnego rzemiosa, bo pomaganie ludziom to moje powoanie.   Ludzie przychodz do mnie z wieloma problemami: nieodwzajemnion mioci, problemami rodzinnymi, brakiem pienidzy, niepowodzeniami, lkami, nieszczciem, chorob. Wszystkie te problemy mona rozwiza, a jeli nie da si ich rozwiza normalnymi sposobami, pomagam magicznymi mocami!",
    "Kiedy pracuj z klientem, powicam duo wewntrznej energii, czasem nawet wicej ni wynosi moja bezpieczna norma; bo chc mie pewno, e naprawd wszystko zrobiam idealnie. Moi klienci otrzymuj rezultaty i szczerze mi dzikuj. Daj mi duo jasnej, wypocztej energii. I czuj si upowaniona do tego, by znw pomaga. ",
  ];

  const imgArr = [aboutMe_1, aboutMe_2, aboutMe_3];

  const dataArr = [];

  for (let i = 0; i < textArr.length; i++) {
    dataArr.push({
      img: imgArr[i],
      text: textArr[i],
    });
  }

  return (
    <section className={classes.aboutMe} id='about_me'>
      <h2 className={classes.aboutMe__title}>O mnie</h2>
      <ul className={classes["aboutMe-list"]}>
        {dataArr.map((item, index) => (
          <li key={index} className={classes["aboutMe-list__item"]}>
            <img src={item.img} alt="photo" />
            <p>{item.text}</p>
          </li>
        ))}
      </ul>
      <a href="#" className={classes.aboutMe__link}>Napisz do mnie!</a>
    </section>
  );
};
